import React from "react";

const AddSingers = ({ image, name, music }) => {
  return (
    <div className="col-md-3 col-sm-6 col-xs-12 mx-auto mycol">
      <div className="singerdiv">
        <div
          style={{ backgroundImage: "url(" + image + ")" }}
          className="singerPhoto"
        />
        <div className="singerDetail">
          <div className="musicType">{music}</div>
          <div className="singername">{name}</div>
          <div className="moreinfo">More Info </div>
          <div className="hash">|</div>
          <div className="ticket">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="20"
              height="20"
              fill="currentColor"
              className="bi bi-bookmark-check"
              viewBox="0 0 16 16"
            >
              <path
                fillRule="evenodd"
                d="M10.854 5.146a.5.5 0 0 1 0 .708l-3 3a.5.5 0 0 1-.708 0l-1.5-1.5a.5.5 0 1 1 .708-.708L7.5 7.793l2.646-2.647a.5.5 0 0 1 .708 0z"
              />
              <path d="M2 2a2 2 0 0 1 2-2h8a2 2 0 0 1 2 2v13.5a.5.5 0 0 1-.777.416L8 13.101l-5.223 2.815A.5.5 0 0 1 2 15.5V2zm2-1a1 1 0 0 0-1 1v12.566l4.723-2.482a.5.5 0 0 1 .554 0L13 14.566V2a1 1 0 0 0-1-1H4z" />
            </svg>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AddSingers;
